.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

body {
  display: flex;
  flex-direction: column; }

header {
  order: 0; }

#breadcrumbs,
#main-wrapper {
  order: 2; }

#search {
  order: 1; }

footer {
  order: 3; }

.sidebar-left #aside {
  order: -1; }

.page-catalog #main-wrapper {
  margin-bottom: 110px; }

#aside-price-range input {
  position: absolute;
  opacity: 0;
  width: 1px;
  height: 1px; }

#price-range-slider {
  margin: 0 8px 57px;
  font-size: 15px; }

#main-menu {
  background: #263238;
  color: #fff;
  font-size: 14px; }
  #main-menu ul {
    display: flex;
    align-items: center;
    height: 60px; }
    #main-menu ul li + li {
      margin-left: 32px; }
  #main-menu a {
    transition: color .3s; }
    #main-menu a:hover {
      color: #8bc34a; }

#breadcrumbs {
  margin-top: 40px;
  margin-bottom: 30px;
  font-size: 15px;
  color: #b0bec5; }
  #breadcrumbs a {
    transition: color .3s; }
    #breadcrumbs a:hover {
      color: #263238; }
  #breadcrumbs span {
    color: #263238; }

#search {
  background: #b5b5b5 url(../img/bg-search.png) 50% 50%/cover;
  text-align: center;
  padding-top: 65px;
  padding-bottom: 70px; }
  .front #search {
    padding-top: 156px;
    padding-bottom: 174px; }
  #search h2 {
    margin-bottom: 34px;
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    line-height: 30px; }
  #search p {
    color: #fff;
    line-height: 30px;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 20px; }
    .front #search p {
      font-weight: 300;
      margin-bottom: 50px; }

#form-search {
  position: relative;
  display: inline-block; }
  #form-search input,
  #form-search button {
    transition: all .3s; }
    #form-search input:focus,
    #form-search button:focus {
      outline: none; }
  #form-search input {
    width: 670px;
    height: 56px;
    padding-left: 25px;
    padding-right: 80px;
    border-radius: 4px;
    border: 1px solid #fff;
    box-shadow: 1px 1px 10px rgba(38, 50, 56, 0.5);
    font-size: 14px;
    font-weight: 500;
    letter-spacing: .1em;
    color: #263238;
    background: #fff; }
    #form-search input:focus {
      border-color: rgba(38, 50, 56, 0.5); }
    #form-search input::placeholder {
      color: #90a4ae; }
    #form-search input::-webkit-input-placeholder {
      color: #90a4ae; }
  #form-search button {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    width: 56px;
    height: 56px;
    border: 0;
    border-radius: 0 4px 4px 0;
    background: transparent;
    color: #90a4ae;
    font-size: 20px; }
    #form-search button:hover, #form-search button:focus, #form-search button:active {
      background: #263238;
      color: #fff; }

#contact-form {
  background: #fff;
  font-family: "roboto", sans-serif; }
  #contact-form .title {
    padding-top: 60px;
    padding-bottom: 21px;
    font-weight: 600;
    font-size: 24px;
    color: #333; }
  #contact-form input,
  #contact-form textarea {
    font-size: 15px;
    font-weight: 400;
    background: #f1f4f5;
    color: #7a7a7a;
    border: none; }
  #contact-form input {
    width: 350px;
    height: 43px;
    padding-left: 17px; }
  #contact-form input + input {
    margin-left: 21px; }
  #contact-form input:focus {
    outline: 0; }
  #contact-form .second-row {
    margin-top: 28px;
    margin-bottom: 22px; }
  #contact-form textarea {
    width: 725px;
    height: 193px;
    resize: none;
    padding: 16px 17px; }

#footer {
  background: #263238;
  font-family: "roboto", sans-serif; }
  #footer .columns {
    padding-top: 115px;
    padding-bottom: 95px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  #footer .title {
    font-size: 22px;
    font-weight: 500;
    color: #fff; }
  #footer .title::after {
    content: '';
    display: block;
    width: 51px;
    height: 3px;
    background: #8bc34a;
    margin-top: 20px;
    margin-bottom: 25px; }
  #footer .copyright {
    border-top: 1px solid #424d53;
    text-align: center; }
    #footer .copyright p {
      padding-top: 27px;
      padding-bottom: 32px;
      font-family: "roboto", sans-serif;
      font-weight: 400;
      font-size: 15px;
      color: #919699; }

#filters {
