    * {
        box-sizing: border-box;
    }
    
    a {
        text-decoration: none;
    }
    /* CUSTOM */
    
    .btn {
        display: inline-block;
        text-align: center;
        margin-bottom: 10px;
        padding: 14px 38px 12px;
        font-family: $font-family-base;
        font-size: 15px;
        line-height: $line-height-base;
        color: $color-light-primary;
    }
    /* BTN SIZE */
    
    .btn-small {
        padding: 11px .5em 10px;
        border-radius: 3px;
        min-width: 72px;
        font-size: 12px;
    }
    
    .btn-medium {
        padding: 11px 21px 10px;
        font-weight: 700;
        font-size: 14px;
    }
    
    .btn-big {
        padding: 16px 38px 15px;
        font-weight: 500;
    }
    
    .btn-bigger {
        padding: 18px 27px 17px;
        font-weight: 500;
    }
    
    .btn-great {
        padding: 19px .5em 18px;
        box-shadow: 0px 1px 3px 0px rgba($shadow-btn, 0.75);
        min-width: 163px;
    }
    
    .btn-block {
        display: block;
        padding-top: 18px;
        padding-bottom: 17px;
        box-shadow: 0px 1px 2px 0px rgba($shadow-btn-hover, 0.75);
        font-weight: 500;
    }
    /* BTN COLOR */
    
    .btn-white {
        background: $color-light-primary;
        color: $color-dark-primary;
        &:hover {
            background: $color-accent-2;
            color: $color-light-primary;
            box-shadow: 0px 2px 5px 0px rgba($shadow-btn, 0.75);
        }
    }
    
    .btn-red {
        background: $color-accent-2;
    }
    
    .btn-green {
        background: $color-accent-1;
    }
    
    .btn-blue {
        background: $color-accent-4;
    }
    
    .btn-gray {
        background: $color-accent-5;
    }